
import React, { useState, useEffect, useContext } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory,
    useRouteMatch,
} from "react-router-dom";
import pagelogo from './logo.png';
import './font-awesome/css/font-awesome.css'
const Headers = () => {
    
    const [popupmneushow, setPopupmneushow] = useState(false);
    const [stcikymenu, setStcikymenu] = useState(false);
    const showMenu = () => {
        setPopupmneushow(true);
    }
    const hideMenu = () => {
        setPopupmneushow(false);
    }
    const showhide = () => {
        if (window.scrollY < 200) {
            //console.log(window.scrollY);
            setStcikymenu(false);
            //console.log('hide');
        } else {
            //console.log(window.scrollY);
            setStcikymenu(true);
            //console.log('show');
        }
    }
    useEffect(() => {
        window.addEventListener('scroll', showhide)

        return () => {
            window.removeEventListener('scroll', showhide);
        }

    }, [])
    return (
        <div className="header_container">
            <div className="header_container_normal">
                <TopContent></TopContent>
                <DesktoptitleNomal></DesktoptitleNomal>
                <DesktoptitleMobile showMenu={showMenu}></DesktoptitleMobile>
                <MenuDesktop></MenuDesktop>
                {stcikymenu ? <ScrollMenu showMenu={showMenu} /> : ''}
                {popupmneushow ? <MblFullPopupMenu hideMenu={hideMenu} /> : ''}
            </div>
        </div>
    )
}

export default Headers;

const DesktoptitleNomal = () => {
    let match = useRouteMatch();

    const his = useHistory();
    const GoLoginpg = () => {
        his.push("/login");
       // console.log('o');
    }
    return (
        <div className="deskmenurow">
            <div className="deskmenucontainer">
                <div className="logocontainer">
                    <img style={{ maxWidth: "150px" }}  src={pagelogo}></img>
                </div>
                  <div className="SearchbuttonContainer">
                   <div className="searchbox">
                        <input className="man_searchbox" placeholder="search....."></input>
                    </div>
                    <i className="fa fa-search"></i> 
                </div>
                <div className="registerbuttoncontainer">
                    <button type="button" onClick={()=>GoLoginpg()} className="btn-main loginorrig">
                        LOGIN/REGISTER
                    </button>
                    <button className="btn-main manpagecartitems">
                        <i className="fa fa-shopping-cart"></i>
                    </button>
                </div>
            </div>
        </div>
    )
}

const TopContent = () => {
    return (
        <div className="header_contact_container">
            <div className="header_main_content">
                <div className="header_main_content_left">
                    <div className="faitemlist">
                        <a href="#">
                            <i className="fa fa-facebook"></i>
                        </a>
                    </div>
                    <div className="faitemlist">
                        <a href="#">
                            <i className="fa fa-twitter"></i>
                        </a>
                    </div>
                    <div className="coopentitle">
                        Use Coupon Code - "newuser10" to get 10% Discount on all products!!!
                    </div>
                </div>
                <div className="header_main_content_right">
                    <div className="header_about_shop">
                        <div className="list_services">
                            <i className="fa fa-location-arrow"></i>
                            120 Minutes delivery
                        </div>
                        <div className="list_services">
                            <i className="fa fa-mobile"></i>
                            +91 9940392396
                        </div>
                        <div className="list_services">
                        <Link to="/contactus"><i className="fa fa-envelope-o"></i> Contact Us</Link>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

const DesktoptitleMobile = ({ showMenu }) => {
    
    return (
        <div className="deskmenurowMbile">
            <div className="deskmenucontainer">
                <div className="sidenavication">
                    <button onClick={showMenu}>
                        <i className="btn-main fa fa-bars"></i>
                    </button>

                </div>
                <div className="logomaindiv">
                    <img style={{ maxWidth: "95px" }} src={pagelogo}></img>
                </div>
                <div className="sidenavication">
                    <i className="btn-main fa fa-shopping-cart"></i>
                </div>
            </div>
        </div>
    )
}

const MenuDesktop = () => {
    return (
        <div className="desktopmenu_row">
            <div className="desktopmenu_container">
                <div className="desktop_menuitems">
                    <div className="desktop_menu_item_list">
                        <Link to="/">HOME</Link>
                    </div>
                    <div className="desktop_menu_item_list">
                        <Link to="/products">PRODUCTS</Link>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

const ScrollMenu = ({ showMenu }) => {
   
    const shomenus = () => {
        console.log("working");
        showMenu();
    }
    return (
        <div className="scroll_menu_container">
            <div className="scroll_menu_row">
                <div className="scroll_menu">
                    <div className="scroll_menu_logo">
                        <img style={{ maxWidth: "80px" }} src={pagelogo}></img>
                    </div>
                    <div className="scroll_menu_items">
                        <div className="desktop_menu_item_list">
                            <Link to="/">HOME</Link>
                        </div>
                        <div className="desktop_menu_item_list">
                            <Link to="/products">PRODUCTS</Link>
                        </div>                        
                    </div>
                    <div className="scroll_menu_mblicon">
                        <button onClick={shomenus}>
                            <i id="show_popupmenu_click" className="btn-main fa fa-bars"></i>
                        </button>

                    </div>
                </div>
            </div>
        </div>

    )
}

const MblFullPopupMenu = ({ hideMenu }) => {
  
    return (
        <div className="popupmenu_container">
            <div className="popupmenu_row">
                <div className="top_closebutton">
                    <div className="title_menu">
                        FRESH CUTS
                    </div>
                    <button className="menu_close" type="button" onClick={() => hideMenu()}>
                        <i className="fa fa-times"></i>                        
                    </button>
                </div>
                <div className="popmenu_items">
                    <div className="desktop_menu_item_list">
                        <Link to="/">HOME</Link>
                    </div>
                    <div className="desktop_menu_item_list">
                        <Link to="/products">PRODUCTS</Link>
                    </div>                    
                    <div className="desktop_menu_item_list">
                        <button className="menu_close" type="button" onClick={() => hideMenu()}>
                            <i className="fa fa-times"></i>
                            Close
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}